//헤더
.hd {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 1.5rem calc(100% - 1580px) 1.5rem calc(100% - 1580px);
	margin: 0 auto;
  text-align: left;
	z-index: 100;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	@media all and (max-width: 1610px) {
		padding: 1.5rem;
	}
  @media all and (max-width: 700px) {
    text-align: center;
  }
  //로고
  .logo {
    float: left;
    font-weight: 600;
    font-size: 1.6rem;
    color: #333;
		cursor: pointer;
		span {
			font-weight: 300;
			font-size: 1rem;
			color: #666;
		}
  }
  //깃헙 페북 링크드인
  .contact {
    float: right;
		margin-top: .25rem;
    @media all and (max-width: 700px) {
      //display: none;
    }
    li {
      float: left;
      span {
				position: relative;
				cursor: pointer;
        color: #555;
				font-weight: 300;
        font-size: .8rem;
				&.active {
					color: #2556a0;
					font-weight: 600;
				}
      }
      &+li {
        margin-left: 1rem;
      }
    }
    @include clearfix;
  }
}
//푸터
.ft {
