//최상위 컨테이너
.content {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 1;
  font-size: 0;
	.swiper-section {
		height: 100%;
		.swiper-wrapper {
			& > .swiper-slide {
				text-align: right;
				&:after {
					content: '';
					display: inline-block;
					width: 0;
					height: 100%;
					vertical-align: middle;
				}
			}
		}
	}
  //스와이퍼 랩핑
  .swiper-wrap {
    display: inline-block;
    vertical-align: middle;
    z-index: 1;
    font-size: 0;
		text-align: left;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
    @media all and (max-width: 1610px) {
      width: 100%;
			padding-left: 1.5rem;
    }
		&.contact-wrap {
			width: 100%;
			padding-left: 0;
		}
		//contact 영역
		.contact-form {
			width: 100%;
			p {
				text-align: center;
				.icon {
					font-size: 3rem;
					color: #2556a0;
				}
			}
			.form-inner {
				width: 22.22rem;
				margin: 0 auto;
				@media all and (max-width: 700px) {
					width: 100%;
					padding: 0 1.5rem;
				}
				.row {
					margin-bottom: 1.5rem;
				}
				.inline-field {
					position: relative;
					display: inline-block;
					width: 100%;
				}
				.field {
					position: relative;
					display: block;
					width: 100%;
					line-height: 1;
				}
				input {
					border: none;
					border-bottom: 1px silver solid;
					height: 2.22rem;
					width: 100%;
					//background-color: #f1f1f1;
					padding-left: .5rem;
					font-size: .8rem;
					&:focus {
						outline: none;
					}
				}
				.input-label {
					position: absolute;
					top: .65rem;
					left: .5rem;
					transition: all .3s;
					font-size: .8rem;
					&.active {
						top: -.8rem;
						font-size: .65rem;
						//transform: scale(.85);
					}
				}
				textarea {
					width: 100%;
					height: 16.66rem;
					font-size: .8rem;
					padding: .7rem .5rem;
					border: none;
					border-bottom: 1px silver solid;
					overflow-y: auto;
					&:focus {
						outline: none;
					}
				}
				.send-btn {
					display: block;
					font-size: .9rem;
					width: 100%;
					height: 3rem;
					background-color: #2556a0;
					color: #fff;
				}
			}
		}
		//about 영역
		.about-list {
			width: 1580px;
			@media all and (max-width: 1610px) {
				width: 100%;
			}
			li {
				opacity: 0;
				width: 33.33%;
				padding-right: 1.5rem;
				float: left;
				-webkit-box-sizing: border-box;
				box-sizing: border-box;
				.h-bar {
					display: inline-block;
					width: 2rem;
					height: 1px;
					background-color: #000;
					vertical-align: middle;
					margin-right: .5rem;
				}
				em {
					display: inline-block;
					font-size: 1rem;
					font-style: normal;
					font-weight: 600;
					color: #000;
					vertical-align: middle;
				}
				p {
					font-size: .8rem;
					font-weight: 300;
					color: #333;
					margin-bottom: .25rem;
					&:first-of-type {
						margin-top: 1.5rem;
					}
				}

				@media all and (max-width: 700px) {
					float: none;
					width: 100%;
					& + li {
						margin-top: 2.5rem;
					}
					p {
						&:first-of-type {
							margin-top: .5rem;
						}
					}
				}
			}
			@include clearfix;
		}
    //스와이퍼 영역
    .swiper-container.swiper-main {
      width: 1580px;
      overflow: visible;
      @media all and (max-width: 1610px) {
        width: 100%;
      }
      @media all and (max-width: 1220px) {
        width: 100%;
      }
      .swiper-wrapper {
        .swiper-slide {
          position: relative;
          background-color: white;
          font-size: 0;
          overflow: hidden;
          .project-title {
						display: block;
            margin-top: .8rem;
						margin-bottom: .3rem;
            font-size: 1rem;
            font-weight: 300;
            color: #222;
          }
          span {
            font-size: .7rem;
            font-weight: 300;
            color: gray;
            letter-spacing: .04rem;
						vertical-align: top;
            & + span {
              margin-left: .5rem;
            }
          }
          .thumbnail-box {
						cursor: pointer;
            width: 100%;
            height: 300px;
						background: {
							position: center center;
							repeat: no-repeat;
							size: cover;
						};
						&.list-1 {
							background-image: url("https://firebasestorage.googleapis.com/v0/b/portfolio-e5c1c.appspot.com/o/list_1.jpg?alt=media&token=7d5e83ce-f842-46e1-a1c2-28472f5b8986");
						}
						&.list-2 {
							background-image: url("https://firebasestorage.googleapis.com/v0/b/portfolio-e5c1c.appspot.com/o/list_2.jpg?alt=media&token=2afe4ca7-9c65-47b5-a390-a041f9126ef5");
						}
						&.list-3 {
							background-image: url("https://firebasestorage.googleapis.com/v0/b/portfolio-e5c1c.appspot.com/o/list_3.jpg?alt=media&token=781c3e26-2239-4659-a28c-7df39d1cb60a");
						}
						&.list-4 {
							background-image: url("https://firebasestorage.googleapis.com/v0/b/portfolio-e5c1c.appspot.com/o/list_4.jpg?alt=media&token=d448ca6b-c4a7-45aa-8f38-f64f53eba6a7");
						}
						&.list-5 {
							background-image: url("https://firebasestorage.googleapis.com/v0/b/portfolio-e5c1c.appspot.com/o/list_5.jpg?alt=media&token=1f6258fb-9789-4a89-a9a6-955716cea21e");
						}
						&.list-6 {
							background-image: url("https://firebasestorage.googleapis.com/v0/b/portfolio-e5c1c.appspot.com/o/list_6.jpg?alt=media&token=82e88072-8a2f-4ebb-b84a-e12c6a2a16b0");
						}
						&.list-7 {
							background-image: url("https://firebasestorage.googleapis.com/v0/b/portfolio-e5c1c.appspot.com/o/list_7.jpg?alt=media&token=cad0e332-bb45-48e4-9f8e-929c3e464087");
						}
            //@for $i from 1 through 7 {
            //  &.list-#{$i} {
            //    background: {
            //      image: url("../images/works/list_#{$i}.jpg");
            //      position: center center;
            //      repeat: no-repeat;
            //      size: cover;
            //    };
            //  }
            //}
          }
          @media all and (max-width: 700px) {
            transition: all .5s;
            &:not(.swiper-slide-active) {
              -webkit-transform: scale(.9);
              -moz-transform: scale(.9);
              -ms-transform: scale(.9);
              -o-transform: scale(.9);
              transform: scale(.9);
              opacity: .8;
            }
          }
        }
      }
    }
    //스와이퍼 버튼 공통, 오버플로우 위해 swiper-container 벗어남
    .swiper-button-next, .swiper-button-prev {
      background: {
        image: none;
      }
      border-radius: 50%;
      width: 2.777rem;
      height: 2.777rem;
      text-align: center;
      &:before {
        content: '';
        display: inline-block;
        vertical-align: middle;
        width: 1.3rem;
        height: 1.3rem;
        border: solid gray;
        border-width: 0 2px 2px 0;
      }
      &:after {
        content: '';
        display: inline-block;
        width: 0;
        height: 100%;
        vertical-align: middle;
      }
    }
    //스와이퍼 우측 버튼
    .swiper-button-next {
      right: -3.333rem;
      &:before {
        transform: rotate(-45deg);
        margin-right: .25rem;
      }
    }
    //스와이터 좌측 버튼
    .swiper-button-prev {
      left: -3.333rem;
      &:before {
        transform: rotate(135deg);
        margin-left: .25rem;
      }
    }
    //스와이퍼 페이징 버튼들
    .swiper-pagination-bullets {
      bottom: -2.2222rem;
      width: 100%;
      .swiper-pagination-bullet {
        width: 1.666rem;
        height: .444rem;
        display: inline-block;
        border-radius: 1.8rem;
        background: rgba(255, 255, 255, .65);
        opacity: 1;
        margin: 0 4px;
        &.swiper-pagination-bullet-active {
          background: #007aff;
        }
      }
    }
  }
  #particles-js {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  &:after {
    content: '';
    display: inline-block;
    width: 0;
    height: 100%;
    vertical-align: middle;
  }
}
.pre-loader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	text-align: center;
	z-index: 500;
	background-color: white;
	&.hide {
		display: none;
	}
	&:after {
		content: '';
		display: inline-block;
		width: 0;
		height: 100%;
		vertical-align: middle;
	}
	.bouncing-loader {
		display: inline-block;
		vertical-align: middle;
		div {
			display: inline-block;
			width: 1rem;
			height: 1rem;
			margin: 3rem 0.2rem;
			background: #8385aa;
			border-radius: 50%;
			animation: bouncing-loader 0.6s infinite alternate;
			&:nth-child(2) {
				animation-delay: 0.2s;
			}
			&:nth-child(3) {
				animation-delay: 0.4s;
			}
		}
	}
	@keyframes bouncing-loader {
		to {
			opacity: 0.1;
			transform: translate3d(0, -1rem, 0);
		}
	}
}
